import fetch from 'utils/fetch';
// 引入公共常量
import publicConst from 'utils/public_const'



/**
 * 删除权限对象
 */
export function deleteBoByDBIdPort(get) {
	return fetch({
		url:publicConst.proxyPath+'deleteBoByDBID',
		method:'get',
		params:get
	})
}


/**
 * 查询分页信息
 */
export function queryBoPort(get) {
	return fetch({
		url:publicConst.proxyPath+'queryFYBoInfo',
		method:'get',
		params:get
	})
}

/**
 * 根据用户信息查询Bo权限信息     USER_ID UPLATFORM
 */
export function queryBoByUserPort(get) {
	return fetch({
		url:publicConst.proxyPath+'queryBoByUser',
		method:'get',
		params:get
	})
}

/**
 * 根据Bo信息查询User信息
 */

export function queryRoleByUser(query) {
  	return fetch({
	    url: publicConst.proxyPath+'queryUserByBoidAndBP',
	    method: 'get',
	    params:query,
  	});
}


/**
 * 新增权限对象
 */
export function saveBoPort(get) {
	return fetch({
		url:publicConst.proxyPath+'saveBo',
		method:'get',
		params:get
	})
}


/**
 * 更新权限对象
 */
export function updateBoPort(get) {
	return fetch({
		url:publicConst.proxyPath+'updateBo',
		method:'get',
		params:get
	})
}


