const validator = {
    // 输入值必须和#password相同
    equalTo: (rule, value, value2, callback) => {
        if (value === '') {
            callback(new Error('请再次输入密码'));
        } else if (value !== value2) {
            callback(new Error('两次输入密码不一致!'));
        } else {
            callback();
        }
    },
    // 金钱验证
    costValidator: (rule, value, callback) => {
        if (!/^[0-9]+(.[0-9]{2})?$/.test(value)) {
            callback(new Error("请输入金额"));
        } else {
            callback();
        }
    },
    // 数字类型验证
    isNumberValid: (rule, value, callback) => {
        if (!value) {
            return callback(new Error('不能为空'));
        }
        let patrn = /^[0-9]*$/;
        if (!patrn.test(value)) {
            callback(new Error('请输入数字值'));
        } else {
            callback();
        }
    },
    //http验证
	isHttpValid: (rule, value, callback) => {
        if (!value) {
            return callback(new Error('不能为空'));
        }
        let patrn = /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/;
        if (!patrn.test(value)) {
            callback(new Error('请输入正确的链接'));
        } else {
            callback();
        }
    },
	//邮箱验证
	isEailValid: (rule, value, callback) => {
        if (!value) {
            return callback(new Error('邮箱不能为空'));
        }
        let patrn = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
        if (!patrn.test(value)) {
            callback(new Error('请输入正确的邮箱'));
        } else {
            callback();
        }
    },
    //禁止中文  
    isProhibitChinaValid: (rule, value, callback) => {
        if (!value) {
            return callback(new Error('不能为空'));
        }
        let patrn =  /^[^\u4e00-\u9fa5]+$/;
        if (!patrn.test(value)) {
            callback(new Error('不能输入中文'));
        } else {
            callback();
        }
    },
};



export default validator;


