<template>
	<div class="rights_wrap">
		<el-row class="back">
			<el-col :span="0.8">
				<a href="javascript:history.back(-1)">
					<span class="el-icon-arrow-left back_span">返回</span>
				</a>
			</el-col>
			<el-col :span="0.4" style="color: #ccc;">|</el-col>
			<el-col :span="2">
				<span >{{planName}}</span>
			</el-col>
		</el-row>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
			<!--<el-form-item label="BoID" prop="BO_ID">
		    	<el-input v-model="ruleForm.BO_ID" style="width:75rem;"></el-input>
		  	</el-form-item>-->
			<el-form-item label="BoName" prop="BO_NAME">
		    	<el-input v-model="ruleForm.BO_NAME" style="width:75rem;"></el-input>
		  	</el-form-item>
		  	<el-form-item label="Bokey" prop="BO_KEY">
		    	<el-input v-model="ruleForm.BO_KEY" style="width:75rem;"></el-input>
		  	</el-form-item>
		  	<el-form-item label="描述" prop="BO_DESC">
		    	<el-input v-model="ruleForm.BO_DESC" style="width:75rem;"></el-input>
		  	</el-form-item>
	  		<el-form-item label="BoValue" >
		    	<el-input v-model="ruleForm.BO_VALUE" style="width:75rem;"></el-input>
		  	</el-form-item>
		  	<el-form-item label="系统" prop="BPLATFORM">
		    	<el-input v-model="ruleForm.BPLATFORM" style="width:75rem;"></el-input>
		  	</el-form-item>
			<el-row>
				<el-col :span="3" :offset="10" style="margin-top: 40px;">
		  			<el-button type="primary" @click="editUpdateFun('ruleForm')" v-if="editId">修改保存</el-button>
		  			<el-button type="primary" @click="creatFun('ruleForm')" v-else>立即创建</el-button>
				</el-col>
			</el-row>
		</el-form>
	</div>
		
</template>

<script>
	import validator from 'utils/validator'  //正则
	import {
		saveBoPort,   //新增
		queryBoPort,	   //编辑初始化
		updateBoPort	//更新保存
	}from 'api/rightsObject.js'
	export default{
		data(){
			return {
				planName:'新建权限对象',
				editId:this.$route.params.id,
				ruleForm:{
					BO_NAME:'',
					BO_KEY:'',
					BO_ID:'',
					BO_VALUE:'',
					BO_DESC:'',
					BPLATFORM:''
				},
				rules:{
					BO_NAME: [
			            { required: true, message: '请输入BoName', trigger: 'blur' },
			            { min: 3, max:20, message: '长度在 3 到 20个字符', trigger: 'blur' }
			        ],
			        BO_KEY: [
			            { required: true, message: '请输入Bokey', trigger: 'blur' },
			        ],
			        BO_ID: [{validator:validator.isProhibitChinaValid , trigger: 'blur'}],
			        BPLATFORM: [
			            { required: true, message: '请输入系统', trigger: 'blur' },
			        ],
				}
			}
		},
		created(){
			if(this.editId){
				this.planName = '编辑权限对象'
				this.editInit()
			}
		},
		methods:{
			//编辑初始化
			editInit(){
				let req = {
					limit:1,    //当前页
	  				offset:5 ,//条数
	  				DB_ID:this.editId
				}
				queryBoPort(req).then( res =>{
					for(var item of res.ALLBO){
						this.ruleForm = item
					}
//					console.log('编辑初始化'+JSON.stringify(res))
				})
			},
			//更新保存
			editUpdateFun(formName){
				this.$refs[formName].validate((valid) => {
		            if (valid) {
		            	let resData = {
		            		DB_ID:this.editId,
		            		BO_NAME:this.ruleForm.BO_NAME, // 数据对象名称
							BO_KEY:this.ruleForm.BO_KEY, // 数据对象KEY
							BO_DESC:this.ruleForm.BO_DESC,
							BO_VALUE:this.ruleForm.BO_VALUE, // 数据对象VALUE
							BO_ID:'',//this.ruleForm.BO_ID
							ADD1:'', // 附加字段1
							ADD2:'',// 附加字段2
							ADD3:'',// 附加字段3
							ADD4:'',// 附加字段4
							ADD5:'',// 附加字段5
							BPLATFORM:this.ruleForm.BPLATFORM // 系统平台
		            	}
		            	updateBoPort(resData).then( res =>{
		            		this.$message.success('修改权限对象成功')
		            		this.$router.push({
		            			name:'RightsList'
		            		})
		            		console.log('更新保存'+JSON.stringify(res))
		            	})
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
			},
			//新增确定
			creatFun(formName){
				this.$refs[formName].validate((valid) => {
		            if (valid) {
		            	let resData = {
		            		BO_NAME:this.ruleForm.BO_NAME, // 数据对象名称
							BO_KEY:this.ruleForm.BO_KEY, // 数据对象KEY
							BO_DESC:this.ruleForm.BO_DESC,
							BO_VALUE:this.ruleForm.BO_VALUE, // 数据对象VALUE
							BO_ID:'',   // this.ruleForm.BO_ID
							ADD1:'', // 附加字段1
							ADD2:'',// 附加字段2
							ADD3:'',// 附加字段3
							ADD4:'',// 附加字段4
							ADD5:'',// 附加字段5
							BPLATFORM:this.ruleForm.BPLATFORM // 系统平台

		            	}
		            	saveBoPort(resData).then( res =>{
		            		this.$message.success('新增权限对象成功')
		            		this.$router.push({
			            		name:'RightsList'
			            	})
//		            		 console.log('新增权限对象'+JSON.stringify(res))
		            	})
		            	
		          	} else {
		            	console.log('error submit!!');
		            	return false;
		          	}
		        });
		    },
		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.rights_wrap{
		background: #FFFFFF;
		padding-bottom: 20px;
		.back{	
			height: 50px;
			line-height: 50px;
			border-bottom: $border;
			font-size:1.4rem;
			div{
				margin-left: 10px;
			}
		}
		.demo-ruleForm{
			margin-top: 20px;
		}
	}
</style>



